body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stop-watch {
  box-sizing: border-box;
  border: 8px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stop-watch__minute-hand, .stop-watch__second-hand {
  transform-origin: top;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%);
}

.stop-watch__minute-hand {
  background-color: #00f;
  width: 2%;
  height: 25%;
  animation: 60s linear infinite move;
}

.stop-watch__second-hand {
  background-color: green;
  width: 1%;
  height: 50%;
  animation: 5s linear infinite move;
}

.stop-watch:before {
  content: "";
  z-index: 1;
  background-color: orange;
  border-radius: 50%;
  width: 10%;
  height: 10%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes move {
  0% {
    transform: rotate(1deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

